import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    newItem: {name:'',cantidad:'',comments:''},
    articulos:[],
    itemEditar:[],
  },
  mutations: {
    // mostrar todos los articulos
    allItems(){
      this.axios.get('/items')
      .then(res=>{this.articulos = res.data})
      .catch(e=>{console.log(e);})
  },
  // agregar un articulo
  addItem(){
    console.log(this.newItem); 
     this.axios.post('new-item',this.newItem)
     .then(res=>{
       this.articulos.push(res.data);
         // Alerta de mensaje
         this.showAlert();
         this.mensaje.texto = 'Notas Agregada!'
         this.mensaje.color = 'success';
     })
     .catch(e=>console.log(e.response))
    this.newItem.name=''
    this.newItem.cantidad=''
    this.newItem.comments=''
 },
 // borrar un articulo
   delet(id){
     this.axios.delete(`/items/${id}`)
     .then(res=> {
       let index = this.articulos.findIndex(item => item._id === res.data._id)
       this.articulos.splice(index,1)

       this.showAlert();
       this.mensaje.color = "success"
       this.mensaje.texto = "nota eliminada"
     })
     .catch(e=>{console.log(e.response);})
   }, 
   /* aditar un articulo */
   edit(id){
     this.axios.get(`/items/${id}`)
     .then(res=>{
       this.itemEditar = res.data
     })
     .catch(e=>{console.log(e.response);})
   },
   changeItem(item){
     this.axios.put(`/items/${item._id}`,item)
     .then(res=>{
       const index = this.articulos.findIndex(n=> n._id === res.data._id)
       this.articulos[index].name = res.data.name
       this.articulos[index].cantidad = res.data.cantidad
       this.articulos[index].comments = res.data.comments

        this.showAlert();
       this.mensaje.color = "success"
       this.mensaje.texto = "nota editada"
     })
     .catch(e=>{console.log(e.response);})
   },
  }, 

  actions: {
   
  },
  modules: {
  }
})
