<template>
  <div class="home">
    <h1>Stock de Libreria</h1>

    <div class="container mt-4">
      <b-button v-b-modal.modal-1 class="btn btn-success mb-4" >Agregar Articulo</b-button>
      <!-- alerta -->
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
      >
      {{mensaje.texto}}
    </b-alert> 

    <!-- tabla -->
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">articulo</th>
            <th scope="col">cantidad</th>
            
            <th scope="col">comentarios</th>
            <th scope="col">accion</th>
          </tr>
        </thead>
        <tbody>
          <tr  v-for= "(item,index) in articulos" :key= "index">
            <th scope="row" class="text-primary">{{item.name}}</th>
            <td> {{item.cantidad}} </td>
           
            <td>{{item.comments}}</td>
            <td>
              <b-button v-b-modal.modal-2 @click= "edit(item._id)" variant="warning" class="mx-4">
                editar
              </b-button>

              <b-button @click= "delet(item._id)" variant="secondary" >
                  borrar
              </b-button>
            </td>
          </tr>
        </tbody>
      </table>
    
    </div>


    <!-- modal para AGREGAR una nota -->
 <b-modal id="modal-1" title="Nuevo Articulo" class="modal-dialog modal-dialog-centered" hide-footer>
    <form @submit.prevent= "addItem()">
      <input type="text" placeholder="articulo" class="form-control my-2" v-model="newItem.name">
      <input type="text" placeholder="cantidad" class="form-control my-2" v-model="newItem.cantidad">
      <input type="text" placeholder="comentarios" class="form-control my-2" v-model="newItem.comments">
      <b-button type="submit" class="btn btn-info my-4 mx-4" @click="$bvModal.hide('modal-1')">Guardar</b-button>
      <b-button type="button" class="btn btn-warning my-4" @click="$bvModal.hide('modal-1')">cancelar</b-button> 
    </form>  
</b-modal>


<!-- modal para EDITAR una nota -->
<b-modal id="modal-2" title="Editar Articulo" class="modal-dialog modal-dialog-centered" hide-footer>
    <form @submit.prevent= "changeItem(itemEditar)">
      <input type="text" placeholder="articulo" class="form-control my-2" v-model="itemEditar.name">
      <input type="text" placeholder="cantidad" class="form-control my-2" v-model="itemEditar.cantidad">
      <input type="text" placeholder="comentarios" class="form-control my-2" v-model="itemEditar.comments">
      <b-button type="submit" class="btn btn-info my-4 mx-4" @click="$bvModal.hide('modal-2')">Editar</b-button>
      <b-button type="button" class="btn btn-warning my-4" @click="$bvModal.hide('modal-2')">cancelar</b-button> 
    </form>  
</b-modal>


   




    
  </div>
</template>

<script>
 
 

 export default {
    
    
  data(){
    return{
      
       articulos: [], 
      newItem:{name:'',cantidad:'',comments:''},
      itemEditar:[],
      // alerta bootstrap
      mensaje: {color: '', texto: ''},
      dismissSecs: 5,
      dismissCountDown: 0,
       
    } 
  },
  created() {
    this.allItems()
  },
  methods: {
   
     
    // mostrar todos los articulos
    allItems(){
      this.axios.get('/items')
      .then(res=>{this.articulos = res.data})
      .catch(e=>{console.log(e);})
  },
  // agregar un articulo
  addItem(){
    console.log(this.newItem); 
     this.axios.post('new-item',this.newItem)
     .then(res=>{
       this.articulos.push(res.data);
         // Alerta de mensaje
         this.showAlert();
         this.mensaje.texto = 'Notas Agregada!'
         this.mensaje.color = 'success';
     })
     .catch(e=>console.log(e.response))
    this.newItem.name=''
    this.newItem.cantidad=''
    this.newItem.comments=''
 },
 // borrar un articulo
   delet(id){
     this.axios.delete(`/items/${id}`)
     .then(res=> {
       let index = this.articulos.findIndex(item => item._id === res.data._id)
       this.articulos.splice(index,1)

       this.showAlert();
       this.mensaje.color = "success"
       this.mensaje.texto = "nota eliminada"
     })
     .catch(e=>{console.log(e.response);})
   }, 
   /* aditar un articulo */
   edit(id){
     this.axios.get(`/items/${id}`)
     .then(res=>{
       this.itemEditar = res.data
     })
     .catch(e=>{console.log(e.response);})
   },
   changeItem(item){
     this.axios.put(`/items/${item._id}`,item)
     .then(res=>{
       const index = this.articulos.findIndex(n=> n._id === res.data._id)
       this.articulos[index].name = res.data.name
       this.articulos[index].cantidad = res.data.cantidad
       this.articulos[index].comments = res.data.comments

        this.showAlert();
       this.mensaje.color = "success"
       this.mensaje.texto = "nota editada"
     })
     .catch(e=>{console.log(e.response);})
   },
     
  
    //  alerta bootstrap
     countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
    
 }
 }







// @ is an alias to /src
/* import Stock from '@/components/StockLibrary.vue' */
/* import HelloWorld from '@/components/HelloWorld.vue' */

</script>
